import React, { useEffect, useRef } from 'react';
import Chart from 'chart.js/auto';

function getChartColor(vegetable) {
    switch (vegetable) {
        case 'carrot':
            return 'orange';
        case 'radish':
            return 'red';
        case 'cucumber':
            return 'green';
        default:
            return 'black';
    }
}

function getChartData(data) {
    const chartData = {};

    //         chartData 
    data.forEach(product => {
        const { name, price, date } = product;
        if (!chartData[name]) {
            chartData[name] = { label: name, data: [], fill: false, borderColor: getChartColor(name), borderWidth: 2 };
        }
        chartData[name].data.push({ x: date, y: price });
    });

    return chartData;
}

function getChartOptions() {
    return {
        responsive: true,
        legend: {
            display: true,
            position: 'bottom'
        },
        scales: {
            xAxes: [{
                type: 'time',
                time: {
                    unit: 'day',
                    tooltipFormat: 'YYYY-MM-DD'
                },
                ticks: {
                    autoSkip: true,
                    maxTicksLimit: 20
                },
                scaleLabel: {
                    display: true,
                    labelString: 'Date'
                }
            }],
            yAxes: [{
                ticks: {
                    beginAtZero: true
                },
                scaleLabel: {
                    display: true,
                    labelString: 'Price'
                }
            }]
        }
    };
}


function ChartComponent(props) {
    const chartRef = useRef(null);

    useEffect(() => {
        const chartData = getChartData(props.data);
        const chartOptions = getChartOptions();

        const myChart = new Chart(chartRef.current, {
            type: 'line',
            data: {
                datasets: Object.values(chartData),
            },
            options: chartOptions,
        });

        return () => {
            myChart.destroy();
        };
    }, [props.data]);

    return <canvas ref={chartRef} />;
}



export default ChartComponent;

